<template>
	<div>
		<div class="header">
			<div class="icon-left" @click="$router.goBack()"><img src="../../static/img/icon/release_icon/return@2x.png"/></div>
			<div>
				。。。
			</div>
		</div>
		<div class="header-null"></div>
		<div class="forum">
			<div class="forum-title">
				<div class="forum-title-name">
					{{info.note_name}}
				</div>
				<div class="forum-title-content">
					<div>
						<img src="../../static/img/icon/my_icon/shot@2x.png"/>
					</div>
					<div v-if="$route.query.type != 'select'">
						<div>{{info.user_name}}</div>
						<div>2018年01月05日</div>
					</div>
					<div class="seleted" v-else>
						<div>红红</div><u>|</u>
						<div>2018年01月05日</div>
					</div>
					<span v-if="$route.query.type != 'select'&& !isFollow" @click="follow">关注</span>
					<span class="active" v-else-if="$route.query.type != 'select'" @click="cancel">已关注</span>
				</div>
			</div>
			<div class="forum-content">
				<div class="forum-content-type" v-if="$route.query.type != 'select'">
					#{{tag.name}}#
				</div>
				<div class="forum-content-title" v-if="$route.query.type != 'select'">
					<div v-html="info.note_content"></div>
				</div>
			</div>
		</div>
		<div class="forum-comment" v-if="commentInfo.list.length != 0">
			<div class="forum-comment-header">
				<img src="../../static/img/icon/find_icon/comment@2x.png"/>
				<div>全部评论（{{commentInfo.total}}）</div>
			</div>
			<div class="forum-comment-list">
				<div class="forum-comment-list-item" v-for="(item, index) in commentInfo.list">
					<div>
						<img src="../../static/img/icon/denglu_gengxin_icon/logo_denglu@2x.png"/>
					</div>
					<div>
						<div class="item-title">红红</div>
						<div class="item-date">2018月01月05日</div>
						<div class="item-huifu">
							回复云溪：你好
						</div>
						<div class="item-item-child">
							云溪：帮忙点赞，标题是请帮忙点赞
						</div>
					</div>
				</div>
			</div>
			<div class="forum-commentMore">
				查看更多评论
			</div>
		</div>
		<div class="fixed-comment">
			<div class="fixed-comment-value" @click="show = true">
				<div>写评论</div>
			</div>
			<div class="fixed-comment-num">
				<div>
					<img src="../../static/img/icon/find_icon/comment@2x.png"/>
					<div>{{info.comment_count}}</div>
				</div>
				<div @click="collect">
					<img src="../../static/img/icon/find_icon/collection@2x.png" v-if="!collectShow"/>
					<img src="../../static/img/icon/find_icon/collection_faxian@3x.png" v-else/>
					<div>{{info.collect_count}}</div>
				</div>
				<div>
					<img src="../../static/img/icon/find_icon/heart_luntan@2x.png" v-if="!likeShow" @click="like"/>
					<img src="../../static/img/icon/find_icon/heart_xuanzhong_luntan@3x.png" v-else/>
					<div>{{info.like_count}}</div>
				</div>
			</div>
		</div>
		<v-common :dialog="show" @hide="hide" @submit="submit" :placeholder="commonName"></v-common>
	</div>
</template>

<script>
	import {getData} from '../service/getData'
	import VCommon from '../base/v-common'
	export default {
		data() {
			return {
				show: false,
				tag: {},
				isFollow: false,
				info: {},
				topic: null,
				commonName: '我也来评论两句',
				collectShow: false,
				likeShow: false,
				commentInfo: {}
			}
		},
		components: {
			VCommon
		},
		methods: {
			init() {
				this.$store.state.isLoading = true
				getData('/note/appFindNotById', {
					token: sessionStorage.token,
					noteId: this.$route.params.id
				}).then(d => {
					this.$store.state.isLoading = false
					this.tag = d.tag
					this.info = d.note
					this.isFollow = d.isLikeCollectComment.isfollow == 0 ? false : true
					this.collectShow = d.isLikeCollectComment.iscollect == 0 ? false : true
					this.likeShow = d.isLikeCollectComment.islike == 0 ? false : true
					this.topic = d.topic
					this.commentInfo = d.comments
				})
			},
			like() {
				getData('/like/appToLike', {
					token: sessionStorage.token,
					pdId: 1,
					noteType: 1
				}).then(d => {
					if(d.code == 100) {
						this.likeShow = true
					}
				})
			},
			hide() {
				this.show = false
			},
			follow() {
				getData('/follow/appAddFollow', {
					token: sessionStorage.token,
					userId: this.info.userId
				}).then(d => {
					if(d.code == 100) {
						mui.toast(d.msg)
						this.isFollow = true							
					}
				})
			},
			cancel() {
				getData('/follow/appDeleteFollow', {
					token: sessionStorage.token,
					userId: this.info.userId
				}).then(d => {
					if(d.code == 100) {
						this.isFollow = false							
					}
				})
			},
			collect() {
				getData('/collect/appAddCollect', {
					token: sessionStorage.token,
					noteId: this.info.note_id,
					type: this.info.note_type
				}).then(d => {
					if(d.code == 100) {
						this.collectShow = true
						this.info.collect_count += 1
					}
				})
			},
			submit(content) {
				this.$store.state.isLoading = true
				if(this.$route.query.type == 'select') {
					getData('/comment/appAddComment', {
						token: sessionStorage.token,
						content: content,
						pdId: this.info.note_id,
						noteType: this.info.note_type
					}).then(d => {
						this.$store.state.isLoading = false
					})		
				} else {
					getData('/comment/appAddComment', {
						token: sessionStorage.token,
						content: content,
						pdId: this.info.note_id,
						noteType: this.info.note_type
					}).then(d => {
						this.$store.state.isLoading = false
					})					
				}
			}
		},
		mounted() {
			this.init()
		}
	}
</script>

<style scoped lang="less">
	.forum {
		padding: 10px;
		background: #fff;
		
		.forum-title {
			border-bottom: 1px solid #e5e5e5;
			
			.forum-title-name {
				font-size: 15px;
				font-weight: bold;
			}
			
			.forum-title-content {
				display: flex;
				margin: 10px 0;
				align-items: center;
				
				div:nth-child(1) {
					display: flex;
					align-items: center;
				}
				
				img {
					width: 40px;
					height: 40px;
					margin-right: 20px;
				}
				
				.seleted {
					display: flex;
					
					div {
						padding: 0 5px;
						
						&:first-child {
							color: #000;
						}
					}
				}
				
				div:nth-child(2) {
					flex: 1;
					
					&:last-child {
						color: #7B7B7B;						
					}
				}
				
				.active {
					background: #ccc;
					color: #7B7B7B;
				}
				
				span {
					padding: 2px 10px;
					color: #fff;
					border-radius: 5px;
					background: #c9eb59;
				}
			}
			
		}
		.forum-content {
				
			.forum-content-type {
				margin: 10px 0;
				color: indianred;
				font-size: 14px;
			}
			
			/*.forum-content-title {
				
			}*/
			
			.forum-content-imgs {
				margin-top: 10px;
				
				img {
					width: 100%;
					margin-bottom: 5px;
				}
			}
		}
	}
	.forum-comment {
		margin-bottom: 50px;
		
		.forum-comment-header {
			margin: 5px 0;
			padding: 5px 10px;
			display: flex;
			align-items: center;
			background: #fff;
			
			img {
				width: 20px;
				height: 20px;
				margin-right: 10px;
			}
		}	
		
		.forum-comment-list {
			
			.forum-comment-list-item {
				background: #fff;
				display: flex;
				padding: 10px;
				border-bottom: 1px solid #EEEEEE;
				
				div:nth-child(1) {
					img {
						width: 40px;
						height: 40px;
						margin-right: 10px;
					}					
				}
				
				div:nth-child(2) {
					flex: 1;
					
					.item-title {
						color: #c9eb59;
						margin-bottom: 5px;
					}
					
					.item-date {
						color: #7B7B7B;
						margin-bottom: 5px;
					}
					
					.item-huifu {
						margin-bottom: 5px;
					}
					
					.item-item-child {
						padding: 10px;
						background: #E5E5E5;
					}
				}
			}
		}	
		
		.forum-commentMore {
			display: flex;
			justify-content: center;
			padding: 10px;
			background: #fff;
		}
	}
	
	.fixed-comment-null {
		padding: 13px;
		width: 100%;
	}
	
	.fixed-comment {
		position: fixed;
		bottom: 0;
		width: 100%;
		border-top: 1px solid #eee;
		display: flex;
		background: #fff;
		padding: 10px 0;
		align-items: center;
		
		.fixed-comment-value {
			padding: 0 5px;
			flex: 1;
			
			div {
				border: 1px solid #eee;
				border-radius: 5px;
				padding: 2px 5px;
				display: flex;
				justify-content: center;
				input {
					font-size: 14px;
				}
			}
		}
		
		.fixed-comment-num {
			flex: 1;
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 0 10px;
			
			div {
				align-items: center;
				display: flex;
			}
			
			img {
				width: 15px;
				height: 15px;
				margin-right: 5px;
			}
		}
	}
</style>


